import React, { lazy, Suspense } from 'react'
import { Redirect, Route, Switch } from 'react-router-dom'
import {
    FooterComponent,
    PageContainer,
    ProductCategoryPage,
    ProductSubcategoryPage,
    DetailPage,
    ShoppingCart,
    PersonalDetails,
    Wishlist,
    CookiesConsent
} from './components'

import { categoriesData } from './data'
import { useCookies } from "react-cookie";

export const useRoutes = () => {
    const [cookies] = useCookies(['user', 'wishlist'])
    const LoadingAnimation = lazy(() => import('./components/PartialElements/LoadingAnimation'))
    const NavbarMenu = lazy(() => import('./components/NavbarMenu'))
    const CartButton = lazy(() => import('./components/CartButton'))
    const MainPage = lazy(() => import('./components/Main/MainPage'))
    const ProductCategoryPage = lazy(() => import('./components/VisualElements/ProductCategoryPage'))
    const ProductSubcategoryPage = lazy(() => import('./components/VisualElements/ProductSubcategoryPage'))
    const DetailPage = lazy(() => import('./components/VisualElements/DetailPage'))
    const ContactsPage = lazy(() => import('./components/ContactsPage/ContactsPage'))
    const ShoppingCart = lazy(() => import('./components/VisualElements/ShoppingCart'))
    const Wishlist = lazy(() => import('./components/VisualElements/Wishlist'))
    const PersonalDetails = lazy(() => import('./components/PersonalDetails/PersonalDetails'))
    const renderLoader = () => <LoadingAnimation />
    return <>
        <Suspense fallback={renderLoader()}><NavbarMenu /></Suspense>
        <Suspense fallback={renderLoader()}><CartButton /></Suspense>
        <CookiesConsent />
        <PageContainer>
            <Switch>
                <Route path="/" exact>
                    <Suspense fallback={renderLoader()}><MainPage /></Suspense>
                </Route>
                <Route path="/contacts" exact>
                    <Suspense fallback={renderLoader()}><ContactsPage /></Suspense>
                </Route>
                {Object.entries(categoriesData.categories).map(([categoryDataKey, categoryDataValue]) =>
                    <Route key={categoryDataValue.route} path={categoryDataValue.route} exact>
                        <Suspense fallback={renderLoader()}><ProductCategoryPage categoryKey={categoryDataKey} categoryValue={categoryDataValue} /></Suspense>
                    </Route>
                )}
                {Object.values(categoriesData.uncategorizedSubcategories).map(subcategoryData =>
                    <Route key={subcategoryData.route} path={subcategoryData.route} exact>
                        <Suspense fallback={renderLoader()}><ProductSubcategoryPage subcategory={subcategoryData} /></Suspense>
                    </Route>
                )}
                {Object.values(categoriesData.categories).map(categoryData =>
                    categoryData.subcategories.map(subcategoryData =>
                        <Route key={subcategoryData.route} path={subcategoryData.route} exact>
                            <Suspense fallback={renderLoader()}><ProductSubcategoryPage subcategory={subcategoryData} parentFilters={categoryData.filters} /></Suspense>
                        </Route>
                    )
                )}
                <Route path="/:productId(\d+)" exact>
                <Suspense fallback={renderLoader()}><DetailPage /></Suspense>
                </Route>
                <Route path="/cart" exact>
                <Suspense fallback={renderLoader()}><ShoppingCart /></Suspense>
                </Route>
                <Route path="/wishlist" exact>
                <Suspense fallback={renderLoader()}><Wishlist /></Suspense>
                </Route>
                {cookies.user !== undefined ? <Route path="/login" exact>
                <Suspense fallback={renderLoader()}><PersonalDetails /></Suspense>
                </Route> : <></>}
                <Redirect to="/" />
            </Switch>
        </PageContainer>
        <FooterComponent />
    </>
}
