import React, { lazy, Suspense } from 'react'
import { categoriesData, metaTagsData } from '../../data'
import { useTranslation } from "react-i18next"

const MainPage = () => {
    const { t } = useTranslation()
    const LoadingAnimation = lazy(() => import('../PartialElements/LoadingAnimation'))
    const MetaTags = lazy(() => import('../PartialElements/MetaTags'))
    const Slider = lazy(() => import('./Slider'))
    const GridView = lazy(() => import('../VisualElements/GridView'))

    const renderLoader = () => <LoadingAnimation />
    return <>
        <MetaTags {...metaTagsData.mainPage} />
        <Suspense fallback={renderLoader()}>
            <Slider />
        </Suspense>
        <h3 style={{ "textAlign": "center", margin: 30 }}>{t("Группы товаров")}</h3>
        <Suspense fallback={renderLoader()}>
            <GridView cardItems={[
                ...Object.values(categoriesData.categories),
                ...Object.values(categoriesData.uncategorizedSubcategories),
            ]} />
        </Suspense>
    </>
}

export default MainPage
